"use client"

import { useState } from "react"
import Link from "next/link"
import { useRouter } from "next/navigation"
import Header from "@/components/header"
import Footer from "@/components/footer"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Utensils, Mail, Lock, ArrowRight, Github } from "lucide-react"

export default function VisitorLoginPage() {
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const router = useRouter()

  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault()
    setIsLoading(true)

    // SIMULASI LOGIN VISITOR
    // Nanti bagian ini akan diganti dengan fetch ke API Laravel Breeze
    setTimeout(() => {
      localStorage.setItem("user_logged_in", "true")
      localStorage.setItem("user_role", "visitor")
      localStorage.setItem("user_name", email.split('@')[0]) // Ambil nama dari email
      
      setIsLoading(false)
      router.push("/") // Redirect ke Beranda setelah login
    }, 1500)
  }

  return (
    <>
      <Header />
      <main className="min-h-screen bg-[#f4e8d1] flex items-center justify-center p-6 py-12">
        <Card className="w-full max-w-md p-8 bg-white rounded-3xl shadow-xl border-none">
          <div className="text-center mb-8">
            <div className="bg-[#a64029]/10 w-16 h-16 rounded-2xl flex items-center justify-center mx-auto mb-4">
              <Utensils className="text-[#a64029] w-8 h-8" />
            </div>
            <h1 className="font-serif text-3xl text-[#3b2f2f] font-bold">Halo, Pecinta Kuliner!</h1>
            <p className="text-[#6e5849] mt-2 text-sm">Masuk untuk mengeksplorasi dan memberikan ulasan rasa.</p>
          </div>

          <form onSubmit={handleLogin} className="space-y-5">
            <div className="space-y-2">
              <label className="text-sm font-bold text-[#3b2f2f] ml-1">Email</label>
              <div className="relative">
                <Mail className="absolute left-4 top-3.5 w-5 h-5 text-[#6e5849]" />
                <Input 
                  type="email" 
                  placeholder="nama@email.com" 
                  className="pl-12 py-6 border-[#ddd] focus:ring-[#a64029] rounded-xl bg-gray-50/50"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  required
                />
              </div>
            </div>

            <div className="space-y-2">
              <div className="flex justify-between items-center ml-1">
                <label className="text-sm font-bold text-[#3b2f2f]">Kata Sandi</label>
                <Link href="#" className="text-xs text-[#a64029] font-semibold hover:underline">Lupa Sandi?</Link>
              </div>
              <div className="relative">
                <Lock className="absolute left-4 top-3.5 w-5 h-5 text-[#6e5849]" />
                <Input 
                  type="password" 
                  placeholder="••••••••" 
                  className="pl-12 py-6 border-[#ddd] focus:ring-[#a64029] rounded-xl bg-gray-50/50"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  required
                />
              </div>
            </div>

            <Button 
              type="submit" 
              disabled={isLoading}
              className="w-full py-7 bg-[#a64029] hover:bg-[#85311e] text-white font-bold rounded-2xl text-lg shadow-lg shadow-[#a64029]/20 transition-all flex gap-2"
            >
              {isLoading ? (
                <div className="h-5 w-5 animate-spin rounded-full border-2 border-white border-t-transparent"></div>
              ) : (
                <>Masuk <ArrowRight className="w-5 h-5" /></>
              )}
            </Button>
          </form>

          <div className="relative my-8">
            <div className="absolute inset-0 flex items-center">
              <span className="w-full border-t border-[#eee]"></span>
            </div>
            <div className="relative flex justify-center text-xs uppercase">
              <span className="bg-white px-2 text-[#6e5849]">Atau masuk dengan</span>
            </div>
          </div>

          <div className="grid grid-cols-1 gap-3">
            <Button variant="outline" className="py-6 rounded-xl border-[#ddd] hover:bg-gray-50 flex gap-3 text-[#3b2f2f] font-semibold">
              <svg className="w-5 h-5" viewBox="0 0 24 24">
                <path d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z" fill="#4285F4"/>
                <path d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z" fill="#34A853"/>
                <path d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l3.66-2.84z" fill="#FBBC05"/>
                <path d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z" fill="#EA4335"/>
              </svg>
              Google
            </Button>
          </div>

          <div className="mt-8 pt-6 border-t border-[#eee] text-center">
            <p className="text-[#6e5849] text-sm font-medium">
              Belum punya akun? {" "}
              <Link href="/register" className="text-[#a64029] font-bold hover:underline">Daftar Sekarang</Link>
            </p>
          </div>
        </Card>
      </main>
      <Footer />
    </>
  )
}